body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
	margin: 0;
	font-family: Arial;
	background: #171c39;
}
.HomeLayout{
	display: -webkit-flex;
	display: flex;
	margin: 0 auto;
	min-height: 100vh;
}
.Media{
	cursor: pointer;
	margin-right: 15px;
	display: inline-block;
	width: 260px;
	vertical-align: top;
}
.Media:hover .Media-cover{
	box-shadow: 0 0 10px rgba(0,0,0,0.3);
}
.Media-cover{
	-webkit-transition: .3s;
	transition: .3s;
	box-shadow: 0 0 5px rgba(0,0,0,.3);
	height: 160px;
}
.Media-image{
	object-fit: cover;
}
.Media-title{
	margin: 5px 0 0 0;
	color: #44546b;
	white-space: normal;
}
.Media-author{
	font-size: 12px;
	margin: 5px 0 0 0;
}
.container{
	font-size: 15px;
	width: 260px;
	border: 1px solid white;
}
.container p{
	margin-top: -20px;
}
.Playlist{
	display: -webkit-flex;
	display: flex;
	white-space: nowrap;
	overflow-y: hidden;
	overflow-x: auto;
	padding-bottom: 70px;
}
.Category {
  margin-bottom: 1em;
}

.Category-title {
  text-transform: uppercase;
  font-size: 20px;
  color: #3f546c;
  margin: 0 0 20px 0;
  letter-spacing: 2px;
  font-weight: bold;
}
.Category-description {
  color: #888b8e;
  font-weight: 300;
  margin: 0;
  font-size: 14px;
}
.Categories{
	overflow-x: hidden;
	padding: 20px 0 20px 20px;
	background:white;
}
.Search {
  max-width: 400px;
  margin-bottom: 20px;
}

.Search-input {
  background: #f2f2f2;
  padding: 20px;
  font-size: 18px;
  font-family: Arial;
  width: 100%;
  border-radius: 10px;
  border:none;
  outline: 0;
  -webkit-transition: .2s;
  transition: .2s;
  box-shadow: 2px 2px 5px rgba(0,0,0,.2);
}

.Search-input:focus {
  box-shadow: 0 0 10px rgba(0,0,0,.5);
}
.Related {
  width: 300px;
}
.Modal {
  max-width: 768px;
  width: 100%;
  margin: 0 auto;
  position: fixed;
  left: 0;
  right: 0;
  top: 100px;
  background:  white;
  padding: 20px;
  border-radius: 20px;
  box-shadow: 0 0 10px rgba(0,0,0,.4);
}

.Modal-close {
  width: 50px;
  height: 50px;
  position: absolute;
  right: -25px;
  top: -25px;
  border-radius: 50%;
  border: none;
  background: #171c30;
  color: white;
  cursor: pointer;
}

.Modal-close:after {
  content: "X";
  font-weight: bold;
  font-size: 30px;
}
.VideoPlayer {
  width: 100%;
  position: relative;
  background: black;
}
.Video{
	width: inherit;
}
.Video video{
	width: inherit;
	vertical-align: middle;
}
.Title{
	position: absolute;
	top: 20px;
	left: 20px;
	background: rgba(0,0,0,0.5);
	color: white;
	padding: 10px 20px;
}

.Title h2{
	margin: 0;
	font-size: 16px;
	font-weight: normal;
}
.PlayPause {
    width: 50px;
    height: 50px;
}
.PlayPause button{
    height: inherit;
    border: none;
    background: transparent;
    outline: 0;
    width: inherit;
    box-sizing: none;
    font-size: 30px;
    cursor: pointer; 
    padding: 0;
}
.Timer {
  font-size: 14px;
  color: white;
  padding: 0 10px 0 0;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
.Timer p {
  margin: 0;
}
.VideoPlayerControls{
  display: -webkit-flex;
  display: flex;
  position: absolute;
  background: rgba(0,0,0, .7);
  bottom: 10px;
  left: 10px;
  right: 10px;
  z-index: 2;
  border-radius: 20px;
  padding: 0 10px;
}
.ProgressBar{
	display: -webkit-flex;
	display: flex;
	-webkit-flex: 1 1;
	        flex: 1 1;
	-webkit-align-items: center;
	        align-items: center;
}
.ProgressBar input{
	width: 100%;
}
.Spinner {
  position: absolute;
  top: 0;
  bottom: 50px;
  left: 0;
  right: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.Spinner span {
  color: white;
  font-size: 20px;
}
.Volume {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 0 15px;
  border: none;
  background: transparent;
  cursor: pointer;
  outline: 0;
  position: relative;

}

.Volume-range {
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  -webkit-transform-origin: left bottom;
          transform-origin: left bottom;
  position: absolute;
  bottom: 50px;
  left: 50px;
  height: 50px;
  display: none;
  background: rgba(0,0,0,.7);
  padding-right: 20px;
  border-radius: 0 20px 20px 0;
}
.Volume input {
  cursor: -webkit-grab;
  cursor: grab;
  background: blue;
  border: 1px solid blue;
}

.Volume:hover .Volume-range {
  display: -webkit-flex;
  display: flex;
}
.FullScreen {
	-webkit-align-items: center;
	        align-items: center;
	display: -webkit-flex;
	display: flex;
	padding-top: 0 10px;
	cursor: pointer;
}
